<template>
  <!-- clipboard.js ==> http://www.clipboardjs.cn -->
  <!-- balloon.css ==> https://github.com/kazzkiq/balloon.css -->
  <!-- tooltips  CSS  ::before ::after  -->
  <!-- :beforCSS2::beforeCSS3 :before::before H5::before -->
  <!-- animate.css ==> https://daneden.github.io/animate.css/ -->
  <span
    data-clipboard-action="copy"
    :data-clipboard-text="content"
    aria-label="Copy content"
    data-balloon-pos="down"
    @click="handleAnimate($event)"
    class="text"
  >{{content}}</span>
  <!--      e.target e.currentTarget -->
</template>

<script>
export default {
  name: "copytext",
  props: {
    content: "",
    animationName: ""
  },
  data() {
    return {};
  },
  mounted() {
    //
    new this.clipboard(".text", {
      target: function(trigger) {
        return trigger.nextElementSibling;
      }
    });
  },
  methods: {
    handleAnimate(e) {
      console.log(e.target);
      console.log(e.currentTarget);
      //copy 
      e.target.setAttribute("aria-label", "Copied");
      this.animateCSS(e.target, this.animationName, () => {
        // Do something after animation
        e.target.setAttribute("aria-label", "Copy content");
      });
    },
    //animate.css  block inline-block 
    animateCSS(node, animationName, callback) {
      node.classList.add("animated", animationName);
      function handleAnimationEnd() {
        node.classList.remove("animated", animationName);
        node.removeEventListener("animationend", handleAnimationEnd);

        if (typeof callback === "function") callback();
      }

      node.addEventListener("animationend", handleAnimationEnd);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.text {
  /* animate.css  block inline-block  */
  display: inline-block;
  padding: 2px 4px;
  color: var(--color-main);;
  cursor: pointer;
}
.text:focus{
  outline: none;
}
</style>
